	module.exports = {"data":{"allTalkJson":{"edges":[{"node":{"description":"Check out our upcoming conferences at http://gotocon.com\nSubscribe to GOTO Conference's YouTube Channel at http://www.youtube.com/user/GOTOConferences?sub_confirmation=1\n\nDouglas Crockford - JSON Creator & Yahoo! JS Architect\n\nABSTRACT\nMuch of programming language design is dictated by fashion. As a consequence, opinions about programming languages tend to be strong, shallow, and deeply emotional. The best languages are brilliant and sadly unfashionable. But we love our stylish, over-rated syntax, and this talk presents an amazing parsing technique that helps achieve it.\n\nhttps://twitter.com/gotocon\nhttps://www.facebook.com/GOTOConference\nhttp://gotocon.com","meta":{"duration":"PT49M40S"},"presenter":{"name":null},"source":{"id":"Nlqv6NtBXcA","provider":"youtube"},"slug":"syntaxation-douglas-crockford","tag":["Programming Language","Syntaxation","Douglas Crockford","GOTO Conference","GOTO Conference","GOTO Conference"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/Nlqv6NtBXcA/default.jpg","height":90,"width":120}},"title":"Syntaxation • Douglas Crockford"}},{"node":{"description":"Google Tech Talk\nJanuary 15, 2013\n(more info below)\n\nPresented by Douglas Crockford\n\nABSTRACT\n\nThe wonders of monads are finally revealed without resorting to Category Theory or Haskell. It turns out that monads are trivially expressed in JavaScript, and are one of the key enablers of Ajax. Monads are amazing. They are simple things, almost trivially implemented, with enormous power to manage complexity. But understanding them is surprisingly difficult, and most people, once they have that ah-ha moment, seem to lose the ability to explain them to others.","meta":{"duration":"PT49M48S"},"presenter":{"name":null},"source":{"id":"b0EF0VTs9Dc","provider":"youtube"},"slug":"monads-and-gonads","tag":["monads","Google Tech Talk","Douglas Crockford","Javascript","Ajax"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/b0EF0VTs9Dc/default.jpg","height":90,"width":120}},"title":"Monads and Gonads"}}]}},"pathContext":{"slug":"douglas-crockford","tag":"Douglas Crockford"}}

